.navbar {
    display: flex;
    align-items: center;
    color: grey;
    height: var(--navbarHeight);
    background-color: #171d23;
    width: 100%;
    position: relative;
    z-index: 2;
}
.wrapper{
    width: 100%;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.wrapper * {
    margin: 10px;
}
.check {
    margin-left: 15px;
}

